/* pollviewpublic.css – дополняет pollviewcreator.css */

.options-form {
  display: flex;
  flex-direction: column;
  gap: 0.75rem;
  margin-bottom: 1.5rem;
}

.option-radio {
  background-color: #fff;
  border: 1px solid #e5e7eb;
  padding: 0.75rem 1rem;
  border-radius: 0.5rem;
  display: flex;
  align-items: center;
  gap: 0.5rem;
  cursor: pointer;
  transition: border-color 0.3s, background-color 0.3s;
}

.option-radio:hover {
  border-color: #6366f1;
  background-color: #f9fafb;
}

.option-radio input[type="radio"] {
  accent-color: #6366f1;
  transform: scale(1.2);
}

.submit-button {
  background-color: #4f46e5;
  color: white;
  padding: 0.75rem 1.25rem;
  border: none;
  border-radius: 0.5rem;
  font-weight: 600;
  font-size: 1rem;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.submit-button:hover {
  background-color: #4338ca;
}

.submit-button:disabled {
  background-color: #a5b4fc;
  cursor: not-allowed;
}

.error-text {
  color: #dc2626;
  font-size: 0.95rem;
  margin-top: 1rem;
}
